<template>
  <div class="audio-player" :style="{ width: width + 'px', height: (height || boxWidth) + 'px' }">
    <audio class="audio-player--audio" controls title="sleep_alone" meta="sleep_alone" :src="audio"></audio>
    <div class="audio-bg">
      <img class="audio-bg--img" :src="img" alt="" />
    </div>
    <div class="audio-bar">
      <div @touchmove="_barTouchmove($event)" @touchend="_barTouchend($event)" class="audio-bar__progress">
        <!-- <div v-show="!loaded" class="small-loading audio-bar__loading"></div> -->
        <div class="audio-bar__progress-box">
          <div class="audio-bar__progress--bg"></div>
          <!-- <div class="audio-bar__progress--now" :class="{'loaded': loaded}" :style="{ width: currentTime/duration*100 + '%' }"></div> -->
          <div class="audio-bar__progress--now" :style="{ width: currentTime/duration*100 + '%' }"></div>
        </div>
      </div>
      <!-- 时长倒计时 -->
      <div class="audio-bar__time" v-html="timeFormatter(remainTime)" v-if="countdown"></div>
      <!-- 实际播放时长 -->
      <div class="audio-bar__time" v-html="timeFormatter(currentTime)" v-else></div>
      <div @click="_audioPlay" :class="['audio-bar__btn', { 'play': !playing, 'pause': playing }]">
      </div>
    </div>
    <div class="audio-bar">

    </div>
  </div>
</template>

<script>
// rem
(function(scope){
  // 下面750对应设计稿的宽度
  // document.body.innerHTML = window.innerWidth;
  var ua = navigator.userAgent.toLocaleLowerCase();
  var eventName = 'onorientationchange' in scope ? 'orientationchange' : 'resize';
  var howLong = /chrome|firefox|ucbrowser|mqqbrowser/.test(ua) || (/safari/.test(ua) && /iphone/.test(ua)) ? 0 : 300;
  var winWidth = document.documentElement.clientWidth;
  var docWidth = window.innerWidth;
  var _width = winWidth < docWidth ? winWidth : docWidth; // 兼容部分奇怪的安卓机
  document.documentElement.style.fontSize = (_width / 750 * 40) + 'px';
  scope.addEventListener(eventName, function(){
    clearTimeout(scope.orientationChangedTimeout);
    scope.orientationChangedTimeout = setTimeout(function(){
      document.documentElement.style.fontSize = (_width / 750 * 40) + 'px';
    }, howLong);
  }, false);
}(window));

export default {
  name: 'audio-player',
  data () {
    return {
      duration: 0,
      totalTime:0,
      currentTime: 0,
      boxWidth: 0,
      progressWidth: 0,
      touching: false,
      playing: false,
      // loaded: true,
    }
  },
  props: {
    audio: {
      type: String
    },
    img: {
      type: String
    },
    width: Number,
    height: Number,
    countdown: Boolean,
    timeFormatter: {
      type: Function,
      default (time) {
        const timeFormat = (num) => {
          if (num < 10) return `0${num}`
          return num
        }
        const hour = timeFormat(Math.floor(time / 3600));
        const minute = timeFormat(Math.floor(time /60 % 60));
        const second = timeFormat(Math.floor(time % 3600 % 60));
        return `${hour}:${minute}:${second}`
      }
    },
  },
  watch: {
    audio(val) {
      const self = this;
      this.currentTime = this.$elAudio.currentTime = 0;
    }
  },
  computed: {
    remainTime() {
      const self = this;
      if (!self.duration || self.duration - self.currentTime < 0) return 0;
      if (!self.currentTime) return self.duration;
      return self.duration - self.currentTime;
    },
  },
  methods: {
    _barTouchmove(e) {
      this.touching = true;
      const target = e.changedTouches[0]
      const diff = target.clientX - this.$elProgress.offsetLeft;
      let currentTime = Math.floor(diff / this.progressWidth * this.duration);
      if (currentTime > this.duration) {
        currentTime = this.duration
      }
      this.currentTime = currentTime
    },
    _barTouchend(e) {
      this.touching = false;
      const target = e.changedTouches[0]
      const diff = target.clientX - this.$elProgress.offsetLeft;
      let currentTime = Math.ceil(diff / this.progressWidth * this.duration);
      if (currentTime > this.duration) {
        currentTime = this.duration
      }
      this.currentTime = this.$elAudio.currentTime = currentTime
      this.play()
    },
    _audioPlay() {
      if (this.$elAudio.paused) {
        this.play()
        return
      }
      this.pause()
    },
    play() {
      const self = this;
      if (self.$elAudio.paused) {
        if (!self.$elAudio.currentTime) self.$emit('on-start');
        self.$elAudio.play();
        // 暂停时触发
        self.$emit('on-play');
        self.playing = !self.$elAudio.paused;
        // if (self.currentTime === 0) self.loaded = false;
      }
    },
    pause() {
      if (!this.$elAudio.paused) {
        this.$elAudio.pause()
        // 暂停时触发
        this.$emit('on-pause');
        this.playing = !this.$elAudio.paused;
      }
    }
  },
  mounted () {
    const self = this;
    self.$nextTick(() => {
      // 初始化背景图宽高
      self.boxWidth = self.$el.clientWidth
      self.$elProgress = self.$el.querySelector('.audio-bar__progress')
      self.progressWidth = self.$elProgress.clientWidth
      // 初始化音频总时长
      self.$elAudio = self.$el.querySelector('.audio-player--audio')
      self.$elAudio.addEventListener('durationchange', () => {
        self.duration = self.$elAudio.duration
        self.audioInterval = setInterval(() => {
          if (!self.touching) {
            self.currentTime = Math.ceil(self.$elAudio.currentTime);
            // if (self.currentTime > 0) self.loaded = true;
            // if (self.countdown) self.totalTime = Math.ceil(self.$elAudio.duration) - self.currentTime;
          }
        }, 1000)
      }, false)
      // self.$elAudio.addEventListener('canplaythrough', () => {
      //   self.loaded = true
      // }, false)
      self.$elAudio.addEventListener('pause', () => {
        self.playing = false
      }, false)
      self.$elAudio.addEventListener('ended', () => {
        // 播放结束时触发
         self.$emit('on-end');
      });
    })
  },
  destroy () {
    clearInterval(this.audioInterval)
  }
}
</script>

<style lang="less" scoped>
@rem: 1/40rem;
/* 所有rem x 40 = 设计稿尺寸*/
.audio-player {
  position: relative;
  background-color: #fff;
}
.audio-player--audio {
  display: none;
  visibility: hidden;
  opacity: 0;
}
.audio-bg {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.audio-bg--img {
  width: 100%;
}
.audio-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding: 0 40*@rem;
}
.audio-bar__time {
  margin-left: 42*@rem;
  color: #eee;
  /*line-height: 36px;*/
  font-size: 24*@rem;
}
.audio-bar__progress {
  position: relative;
  flex: 1;
  margin: 37/40rem 0;
  height: 18*@rem;
}
.audio-bar__loading {
  position: absolute;
  z-index: 1;
  left: -15*@rem;
  top: 0;
}
.audio-bar__progress-box {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}
.audio-bar__progress--bg {
  height: 5*@rem;
  background-color: #777;
  border-radius: 2.5*@rem;
}
.audio-bar__progress--now {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 5*@rem;
  background-color: #fff;
  border-radius: 2.5*@rem;
}
.audio-bar__progress--now:after {
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(50%, -50%);
  width: 18*@rem;
  height: 18*@rem;
  border-radius: 50%;
  background-color: #fff;
}
.audio-bar__btn {
  position: absolute;
  left: 40*@rem;
  bottom: 92*@rem;
  width: 80*@rem;
  height: 80*@rem;
  color: #fff;
  -webkit-tap-highlight-color: transparent;
}
.audio-bar__btn.play {
  background: url('data:image/png;base64,') no-repeat;
  background-size: 100% 100%;
}
.audio-bar__btn.pause {
  background: url('data:image/png;base64,') no-repeat;
  background-size: 100% 100%;
}
.small-loading {
  width: 16*@rem;
  height: 16*@rem;
  display: inline-block;
  vertical-align: middle;
  -webkit-animation: weuiLoading 1s steps(12, end) infinite;
  animation: weuiLoading 1s steps(12, end) infinite;
  background: transparent url("data:image/svg+xml;charset=utf8, %3Csvg xmlns='http://www.w3.org/2000/svg' width='120' height='120' viewBox='0 0 100 100'%3E%3Cpath fill='none' d='M0 0h100v100H0z'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23E9E9E9' rx='5' ry='5' transform='translate(0 -30)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23989697' rx='5' ry='5' transform='rotate(30 105.98 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%239B999A' rx='5' ry='5' transform='rotate(60 75.98 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23A3A1A2' rx='5' ry='5' transform='rotate(90 65 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23ABA9AA' rx='5' ry='5' transform='rotate(120 58.66 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23B2B2B2' rx='5' ry='5' transform='rotate(150 54.02 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23BAB8B9' rx='5' ry='5' transform='rotate(180 50 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23C2C0C1' rx='5' ry='5' transform='rotate(-150 45.98 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23CBCBCB' rx='5' ry='5' transform='rotate(-120 41.34 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23D2D2D2' rx='5' ry='5' transform='rotate(-90 35 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23DADADA' rx='5' ry='5' transform='rotate(-60 24.02 65)'/%3E%3Crect width='7' height='20' x='46.5' y='40' fill='%23E2E2E2' rx='5' ry='5' transform='rotate(-30 -5.98 65)'/%3E%3C/svg%3E") no-repeat;
  background-size: 100%;
}
@-webkit-keyframes weuiLoading {
  0% {
    transform: rotate3d(0, 0, 1, 0deg);
  }

  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}

@keyframes weuiLoading {
  0% {
    transform: rotate3d(0, 0, 1, 0deg);
  }

  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}
</style>


